  display: flex;
}

.tabs ul li a {
  color: #bbb;
  width: 100%;
  height: 100%;
  place-items: center;
  text-decoration: none;
  display: grid;
}

.tabs ul li a span {
  font-weight: 700;
}

.tabs ul li {
